"use client"

import { useState, useEffect } from "react"
import { motion, AnimatePresence } from "framer-motion"
import Link from "next/link"

const images = [
  "/beira-rio.jpg",
  "/moleca.jpg",
  "/modare.jpg",
]

export default function Hero() {
  const [index, setIndex] = useState(0)

  useEffect(() => {
    const interval = setInterval(() => {
      setIndex((prev) => (prev + 1) % images.length)
    }, 4000) // Troca a cada 4 segundos

    return () => clearInterval(interval)
  }, [])

  return (
    <section className="relative h-[500px] md:h-[600px] flex items-center justify-center overflow-hidden">

      {/* Background Slider */}
      <AnimatePresence>
        <motion.div
          key={index}
          className="absolute inset-0 bg-cover bg-center"
          style={{ backgroundImage: `url(${images[index]})` }}
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          exit={{ opacity: 0 }}
          transition={{ duration: 1 }}
        />
      </AnimatePresence>

      {/* Overlay */}
      <div className="absolute inset-0 bg-black/30" />

      {/* Content */}
      <div className="relative z-10 text-center max-w-3xl mx-auto px-4">
        <p className="text-secondary text-sm md:text-base tracking-widest uppercase mb-4">
          Bem-vinda à Saroma
        </p>

        <h1 className="text-4xl md:text-6xl font-serif text-white mb-6 font-bold leading-tight text-balance">
          Moda que <span className="text-accent">celebra</span> você
        </h1>

        <p className="text-base md:text-lg text-secondary mb-8 text-balance max-w-2xl mx-auto">
          Descubra nossas coleções exclusivas para todos os estilos e tamanhos.
          Da moda adulta ao infantil, aqui você encontra tudo que precisa.
        </p>

        <div className="flex gap-4 justify-center flex-wrap">
          <Link
            href="#categorias"
            className="px-8 py-3 bg-primary text-primary-foreground rounded-lg hover:bg-accent transition-colors font-medium"
          >
            Explorar Coleção
          </Link>

          <Link
            href="https://wa.me/5544984147413"
            target="_blank"
            rel="noopener noreferrer"
            className="px-8 py-3 border-2 border-white text-white hover:bg-white hover:text-foreground transition-colors font-medium rounded-lg"
          >
            Fale conosco
          </Link>
        </div>
      </div>

    </section>
  )
}
