'use client'

import { useState } from 'react'
import Link from 'next/link'
import { Menu, X } from 'lucide-react'

export default function Header() {
  const [isOpen, setIsOpen] = useState(false)

  return (
    <header className="sticky top-0 z-50 bg-background border-b border-border">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center h-20">
          {/* Logo */}
          <Link href="/" className="flex-shrink-0">
            <div className="text-2xl font-serif font-bold text-primary tracking-tight">
              SAROMA
            </div>
          </Link>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex gap-8">
            <Link href="#categorias" className="text-foreground hover:text-primary transition-colors">
              Categorias
            </Link>
            <Link href="#instagram" className="text-foreground hover:text-primary transition-colors">
              Instagram
            </Link>
            <Link href="#sobre" className="text-foreground hover:text-primary transition-colors">
              Sobre
            </Link>
            <Link href="#contato" className="text-foreground hover:text-primary transition-colors">
              Contato
            </Link>
          </nav>

          {/* Mobile Menu Button */}
          <button
            onClick={() => setIsOpen(!isOpen)}
            className="md:hidden p-2"
            aria-label="Menu"
          >
            {isOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* Mobile Navigation */}
        {isOpen && (
          <nav className="md:hidden pb-4 flex flex-col gap-4">
            <Link href="#categorias" className="text-foreground hover:text-primary">
              Categorias
            </Link>
            <Link href="#instagram" className="text-foreground hover:text-primary">
              Instagram
            </Link>
            <Link href="#sobre" className="text-foreground hover:text-primary">
              Sobre
            </Link>
            <Link href="#contato" className="text-foreground hover:text-primary">
              Contato
            </Link>
          </nav>
        )}
      </div>
    </header>
  )
}
